import React, { useState, useEffect, useRef } from 'react';
import './DateScroller.css';

const DateScroller = ({ selectedDate, onDateChange }) => {
  const [dates, setDates] = useState([]);
  const containerRef = useRef(null);

  useEffect(() => {
    // Initialize dates to show 10 days centered around the selected date
    const initialDates = generateDates(-5, 5);
    setDates(initialDates);
  }, [selectedDate]);

  useEffect(() => {
    // Center the selected date
    centerSelectedDate();
  }, [dates, selectedDate]);

  const generateDates = (startOffset, endOffset) => {
    const newDates = [];
    for (let i = startOffset; i <= endOffset; i++) {
      const date = new Date(selectedDate);
      date.setDate(selectedDate.getDate() + i);
      newDates.push(date);
    }
    return newDates;
  };

  const centerSelectedDate = () => {
    const selectedIndex = dates.findIndex(
      (date) => date.toDateString() === selectedDate.toDateString()
    );
    if (selectedIndex !== -1 && containerRef.current) {
      const dateItemWidth = containerRef.current.scrollWidth / dates.length;
      const scrollTo = dateItemWidth * selectedIndex - containerRef.current.clientWidth / 2 + dateItemWidth / 2;
      containerRef.current.scrollTo({ left: scrollTo, behavior: 'smooth' });
    }
  };

  const scrollLeft = () => {
    if (containerRef.current) {
      containerRef.current.scrollBy({ left: -100, behavior: 'smooth' });
    }
  };

  const scrollRight = () => {
    if (containerRef.current) {
      containerRef.current.scrollBy({ left: 100, behavior: 'smooth' });
    }
  };

  const formatDay = (date) => {
    return date.getDate();
  };

  const formatWeekday = (date) => {
    return date.toLocaleDateString('en-US', { weekday: 'short' });
  };

  const handleDateClick = (date) => {
    onDateChange(date);
  };

  return (
    <div className="date-scroller">
      <button className="scroll-button" onClick={scrollLeft}>&lt;</button>
      <div className="dates-container" ref={containerRef}>
        {dates.map((date) => (
          <div
            key={date.toISOString()}
            className={`date-item ${date.toDateString() === selectedDate.toDateString() ? 'selected' : ''}`}
            onClick={() => handleDateClick(date)}
          >
            <div className="weekday">{formatWeekday(date)}</div>
            <div className="day">{formatDay(date)}</div>
          </div>
        ))}
      </div>
      <button className="scroll-button" onClick={scrollRight}>&gt;</button>
    </div>
  );
};

export default DateScroller;
