import React, { useState, useCallback } from 'react';
import axios from 'axios';
import debounce from 'lodash/debounce';
import { Pie, Bar } from 'react-chartjs-2';
import { Chart as ChartJS, ArcElement, BarElement, CategoryScale, LinearScale, Tooltip, Legend } from 'chart.js';
import DateScroller from './DateScroller';
import "react-datepicker/dist/react-datepicker.css";
import './App.css';
import ChartDataLabels from 'chartjs-plugin-datalabels';

ChartJS.register(ArcElement, BarElement, CategoryScale, LinearScale, Tooltip, Legend, ChartDataLabels);

const API_KEY = process.env.REACT_APP_API_KEY;

function App() {
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [foodItems, setFoodItems] = useState({});
  const [searchTerm, setSearchTerm] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const searchFood = async (query) => {
    if (!query) {
      setSearchResults([]);
      setIsLoading(false);
      return;
    }
    setIsLoading(true);
    try {
      const response = await axios.get(
        `https://api.nal.usda.gov/fdc/v1/foods/search?api_key=${API_KEY}&query=${query}&pageSize=5`
      );
      setSearchResults(response.data.foods);
    } catch (error) {
      console.error('Error searching for food:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const debouncedSearch = useCallback(
    debounce((query) => searchFood(query), 300),
    []
  );

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value);
    debouncedSearch(e.target.value);
  };

  const addFoodItem = (item) => {
    const calories = getNutrientValue(item.foodNutrients, 'Energy');
    const protein = getNutrientValue(item.foodNutrients, 'Protein');
    const carbs = getNutrientValue(item.foodNutrients, 'Carbohydrate, by difference');
    const fat = getNutrientValue(item.foodNutrients, 'Total lipid (fat)');

    const dateKey = selectedDate.toISOString().split('T')[0];
    const newItem = { 
      id: Date.now(),
      name: item.description,
      baseCalories: calories,
      baseProtein: protein,
      baseCarbs: carbs,
      baseFat: fat,
      calories,
      protein,
      carbs,
      fat,
      servings: 1  // Ensure this is always a number
    };

    setFoodItems(prevItems => ({
      ...prevItems,
      [dateKey]: [...(prevItems[dateKey] || []), newItem]
    }));

    setSearchTerm('');
    setSearchResults([]);
  };

  const removeFoodItem = (id) => {
    const dateKey = selectedDate.toISOString().split('T')[0];
    setFoodItems(prevItems => ({
      ...prevItems,
      [dateKey]: prevItems[dateKey].filter(item => item.id !== id)
    }));
  };

  const getNutrientValue = (nutrients, nutrientName) => {
    const nutrient = nutrients.find(n => n.nutrientName === nutrientName);
    return nutrient ? Math.round(nutrient.value) : 0;
  };

  const calculateTotalMacros = () => {
    const dateKey = selectedDate.toISOString().split('T')[0];
    const items = foodItems[dateKey] || [];
    return items.reduce((total, item) => {
      total.calories += item.calories;
      total.protein += item.protein;
      total.carbs += item.carbs;
      total.fat += item.fat;
      return total;
    }, { calories: 0, protein: 0, carbs: 0, fat: 0 });
  };

  const calculateWeeklyMacros = () => {
    const weekMacros = Array(7).fill({ calories: 0, protein: 0, carbs: 0, fat: 0 });
    const currentDate = new Date(selectedDate);
    currentDate.setDate(currentDate.getDate() - currentDate.getDay()); // Start from Sunday

    for (let i = 0; i < 7; i++) {
      const dateKey = currentDate.toISOString().split('T')[0];
      const items = foodItems[dateKey] || [];
      weekMacros[i] = items.reduce((total, item) => {
        total.calories += item.calories;
        total.protein += item.protein;
        total.carbs += item.carbs;
        total.fat += item.fat;
        return total;
      }, { calories: 0, protein: 0, carbs: 0, fat: 0 });
      currentDate.setDate(currentDate.getDate() + 1);
    }
    return weekMacros;
  };

  const macroData = calculateTotalMacros();
  const weeklyMacros = calculateWeeklyMacros();

  const chartData = {
    labels: ['Protein', 'Carbs', 'Fat'],
    datasets: [
      {
        data: [macroData.protein, macroData.carbs, macroData.fat],
        backgroundColor: ['#FF6384', '#36A2EB', '#FFCE56'],
        hoverBackgroundColor: ['#FF6384', '#36A2EB', '#FFCE56']
      }
    ]
  };

  const weeklyChartData = {
    labels: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
    datasets: [
      {
        label: 'Protein',
        data: weeklyMacros.map(day => day.protein),
        backgroundColor: '#FF6384',
        stack: 'stack1', // Add stack identifier
      },
      {
        label: 'Carbs',
        data: weeklyMacros.map(day => day.carbs),
        backgroundColor: '#36A2EB',
        stack: 'stack1', // Add stack identifier
      },
      {
        label: 'Fat',
        data: weeklyMacros.map(day => day.fat),
        backgroundColor: '#FFCE56',
        stack: 'stack1', // Add stack identifier
      }
    ]
  };

  const formatDate = (date) => {
    return date.toISOString().split('T')[0];
  };

  const options = {
    plugins: {
      datalabels: {
        display: true,
        color: 'white',
        formatter: (value, context) => {
          return `${value.toFixed(0)}`; // Display label and percentage
        }
      }
    }
  };

  const totalCalories = macroData.protein * 4 + macroData.carbs * 4 + macroData.fat * 9;

  const percentageData = {
    labels: ['Protein', 'Carbs', 'Fat'],
    datasets: [
      {
        data: [
          (macroData.protein * 4 / totalCalories) * 100,
          (macroData.carbs * 4 / totalCalories) * 100,
          (macroData.fat * 9 / totalCalories) * 100
        ],
        backgroundColor: ['#FF6384', '#36A2EB', '#FFCE56'],
        hoverBackgroundColor: ['#FF6384', '#36A2EB', '#FFCE56']
      }
    ]
  };

  const totalGramsOptions = {
    plugins: {
      title: {
        display: true,
        text: 'Total Grams of Macros'
      },
      datalabels: {
        display: true,
        color: 'white',
        formatter: (value, context) => {
          const label = context.chart.data.labels[context.dataIndex];
          return `${label}: ${value}g`; // Display label and grams
        }
      }
    }
  };

  const percentageOptions = {
    plugins: {
      title: {
        display: true,
        text: 'Percent of Calories'
      },
      datalabels: {
        display: true,
        color: 'white',
        formatter: (value, context) => {
          const label = context.chart.data.labels[context.dataIndex];
          return `${label}: ${value.toFixed(1)}%`; // Display label and percentage
        }
      }
    }
  };

  const updateServings = (id, newServings) => {
    const dateKey = selectedDate.toISOString().split('T')[0];
    setFoodItems(prevItems => ({
      ...prevItems,
      [dateKey]: prevItems[dateKey].map(item => {
        if (item.id === id) {
          // Convert newServings to a number, default to 1 if invalid
          const servings = Math.max(1, parseInt(newServings, 10) || 1);
          return {
            ...item,
            servings,
            calories: Math.round(item.baseCalories * servings),
            protein: Math.round(item.baseProtein * servings * 10) / 10,
            carbs: Math.round(item.baseCarbs * servings * 10) / 10,
            fat: Math.round(item.baseFat * servings * 10) / 10
          };
        }
        return item;
      })
    }));
  };

  return (
    <div className="App">
      <header className="app-header">
        <div className="header-content">
          <div className="logo">Calorie Logger</div>
          <nav className="main-nav">
            <ul className="nav-links">
              <li><a href="#home">Home</a></li>
              <li><a href="#log">Food Log</a></li>
              <li><a href="#stats">Statistics</a></li>
              <li><a href="#profile">Profile</a></li>
            </ul>
          </nav>
          <div className="user-actions">
            <button className="btn-primary">Sign Up</button>
            <button className="btn-secondary">Log In</button>
          </div>
        </div>
      </header>

      <main className="app-main">
        <DateScroller selectedDate={selectedDate} onDateChange={setSelectedDate} />
        <div className="content-wrapper">
          <div className="left-column">
            <div className="search-container">
              <input
                type="text"
                value={searchTerm}
                onChange={handleSearchChange}
                placeholder="Search for a food item"
                className="search-input"
              />
              {isLoading && <div className="loading-spinner"></div>}
              
              {searchResults.length > 0 && (
                <ul className="search-results">
                  {searchResults.map((item) => (
                    <li key={item.fdcId} onClick={() => addFoodItem(item)}>
                      <div className="food-item">
                        <span className="food-name">{item.description}</span>
                        <div className="food-nutrients">
                          <span>{getNutrientValue(item.foodNutrients, 'Energy')} cal</span>
                          <span className="nutrient-divider">|</span>
                          <span>{getNutrientValue(item.foodNutrients, 'Protein')}g protein</span>
                          <span className="nutrient-divider">|</span>
                          <span>{getNutrientValue(item.foodNutrients, 'Carbohydrate, by difference')}g carb</span>
                          <span className="nutrient-divider">|</span>
                          <span>{getNutrientValue(item.foodNutrients, 'Total lipid (fat)')}g fat</span>
                        </div>
                      </div>
                    </li>
                  ))}
                </ul>
              )}
            </div>
            <div className="food-list">
              <h2>Food Items for {selectedDate.toDateString()}</h2>
              {foodItems[formatDate(selectedDate)] && foodItems[formatDate(selectedDate)].length > 0 ? (
                <ul>
                  {foodItems[formatDate(selectedDate)].map((item) => (
                    <li key={item.id} className="added-food-item">
                      <div className="food-item">
                        <span className="food-name">{item.name}</span>
                        <div className="food-nutrients">
                          <span>{Math.round(item.calories)} cal</span>
                          <span className="nutrient-divider">|</span>
                          <span>{Math.round(item.protein)}g protein</span>
                          <span className="nutrient-divider">|</span>
                          <span>{Math.round(item.carbs)}g carb</span>
                          <span className="nutrient-divider">|</span>
                          <span>{Math.round(item.fat)}g fat</span>
                        </div>
                      </div>
                      <div className="item-controls">
                        <input
                          type="number"
                          value={isNaN(item.servings) ? '' : item.servings}
                          min="1"
                          onChange={(e) => updateServings(item.id, e.target.value)}
                          className="serving-input"
                        />
                        <button className="remove-item" onClick={() => removeFoodItem(item.id)}>×</button>
                      </div>
                    </li>
                  ))}
                </ul>
              ) : (
                <p className="no-foods-message">No Foods Added</p>
              )}
            </div>
          </div>
          <div className="right-column">
            <h2>Macronutrient Breakdown</h2>
            <div className="macro-summary">
              <p>Total Calories: {macroData.calories}</p>
              <p>Protein: {macroData.protein}g</p>
              <p>Carbs: {macroData.carbs}g</p>
              <p>Fat: {macroData.fat}g</p>
            </div>
            <div className="chart-container">
              <div className="chart-wrapper">
                <Pie data={chartData} options={totalGramsOptions} />
              </div>
              <div className="chart-wrapper">
                <Pie data={percentageData} options={percentageOptions} />
              </div>
            </div>
          </div>
        </div>
      </main>

      <footer className="app-footer">
        <div className="footer-content">
          <div className="footer-logo">Calorie Logger</div>
          <nav className="footer-nav">
            <ul className="footer-links">
              <li><a href="#about">About</a></li>
              <li><a href="#contact">Contact</a></li>
              <li><a href="#privacy">Privacy</a></li>
              <li><a href="#terms">Terms</a></li>
            </ul>
          </nav>
          <div className="social-links">
            <a href="#facebook" aria-label="Facebook"><i className="fab fa-facebook-f"></i></a>
            <a href="#twitter" aria-label="Twitter"><i className="fab fa-twitter"></i></a>
            <a href="#instagram" aria-label="Instagram"><i className="fab fa-instagram"></i></a>
          </div>
        </div>
      </footer>
    </div>
  );
}

export default App;
